import { Link, withPrefix } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"

const brandStyles = {
  position: "absolute",
  top: `8px`,
  paddingLeft: `1.0875rem`,
}

const Header = ({ siteTitle, activeIndex }) => (
  <Navbar bg="dark" variant="dark" expand="lg" fixed="top" collapseOnSelect>
    <Navbar.Brand style={brandStyles} href={withPrefix("/")}>Hard Stuck Iron</Navbar.Brand>
    <Navbar.Toggle className="ml-auto"/>
    <Navbar.Collapse>
      <Nav className="mx-auto" justify>
        <Nav.Link active={activeIndex === 'home'} href={withPrefix("/")} className="mx-2">Home</Nav.Link>
        <Nav.Link active={activeIndex === 'champions'} href={withPrefix("/champions/")} className="mx-2">Champions</Nav.Link>
        <Nav.Link active={activeIndex === 'summoners'} disabled={true} className="mx-2">Summoners</Nav.Link>
        {/* <Nav.Link active={activeIndex === 'stats'}>Stats</Nav.Link> */}
      </Nav>
    </Navbar.Collapse>
  </Navbar>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
  activeIndex: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  activeIndex: `home`,
}

export default Header
